import { BigdataLogoWhite } from "@rp/react/common/assets"
import { Button, Typography } from "@rp/react/web/mui/core"
import { ErrorPageStyled, classes } from "./ErrorPage.styles"

export function ErrorPage() {
  const handleClick = () => {
    window.location.replace("/")
  }

  const onReload = () => {
    window.location.reload()
  }

  return (
    <ErrorPageStyled className={classes.errorPageRoot}>
      <BigdataLogoWhite
        title="bigdata-logo"
        width="225px"
        data-testid="bigdata-logo"
        className={classes.img}
      />

      <Typography variant="h2" className={classes.oops}>
        <strong>Oops!</strong>
      </Typography>

      <Typography variant="h1" className={classes.something}>
        Something happened.
      </Typography>

      <div>
        <Typography variant="subtitle1">What would you like to do now?</Typography>
        <Button
          data-testid="go-to-home"
          onClick={handleClick}
          type="button"
          variant="contained"
          className={classes.button}
        >
          <strong>Go to Homepage</strong>
        </Button>
        <Button className={classes.button} type="button" variant="outlined" onClick={onReload}>
          <strong>Reload Page</strong>
        </Button>
      </div>
    </ErrorPageStyled>
  )
}
